@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,500&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  margin: 0px;
  padding: 0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 40px 24px;
}

.app__searchform{
  margin-top: 18px;
}

.app__input{
  padding: 6px 16px;
  min-width: 10vw;
  max-width: 25vw;
  font-size: 15px;
}

.app__submit{
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: #02aff0;
  border: 2px solid #02aff0;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}

.app__submit:hover{
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: white;
  border: 2px solid #02aff0;
  border-radius: 4px;
  color: #02aff0;
  cursor: pointer;
}

.app__recipes{
  margin-top: 24px;
  /* display: grid;
  grid-template-columns: 230px 230px; */
  -webkit-align-items: baseline;
          align-items: baseline;
  width: 70%;
}

.app__healthLabels{
  padding: 7px 8px;
  margin-left: 6px;
  border-radius: 4px;
}

.app__healthLabels option{
  text-transform: capitalize;
}
.recipeTile{
    text-align: center;
}

.recipeTile__img{
    height: 200px;
    width: 200px;
    object-fit: cover;
    border-radius: 4px;
    cursor: pointer;
}

.recipeTile__name{
    text-align: center;
    margin: 12px 0px;
}
